.App {
  font-family: sans-serif;
  text-align: center;
}
#flexProperty {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #5ceaea;
}
#h3 {
  margin: 20px;
}
button {
  border: #482323;
  background-color: #03a9f452;
  padding: 10px 15px;
  margin: 30px;
}

#flexContent {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 35px;
}
#avatar {
  border-radius: 250px;
}
